* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}

.home {
    width: 100%;
}

.home .top_banner {
    max-width: 100%;
    height: 380px;
    background: url('/images/homeslide1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 50px 60px;
}

.home .top_banner .content {
    width: 100%;
    position: relative;
    top: 25%;
    padding: 10px 50px;
}

.home .top_banner .content h3 {
    text-transform: capitalize;
    color: #cd1e76;
    font-weight: 300;
    letter-spacing: 1px;
}

.home .top_banner .content h2 {
    text-transform: uppercase;
    color: #232323;
    letter-spacing: 1px;
    font-size: 40px;
    margin-top: 10px;
}

.home .top_banner .content p {
    color: #232323;
    letter-spacing: 1px;
    margin-top: 10px;
}

.home .top_banner .content p {
    color: #232323;
    letter-spacing: 1px;
    margin-top: 10px;
    margin-bottom: 30px;
}

.home .top_banner .content .link {
    padding: 10px 30px;
    background: #fed300;
    color : #232323;
    text-decoration: none;
    border-radius: 5px;
    transition: 0.5s;
}

.home .top_banner .content .link:hover {
    color: #fed300;
    background: #cd1e76;
}

.home .trending {
    padding: 20px 30px;
    width: 100%;
}

.home .trending .container {
    max-width: 100%;
    display: flex;
}

.home .trending .container .left_box {
    padding: 10px 20px;
    width: 75%;   
}

.home .trending .container .left_box .header {
    display: flex;
    max-width: 100%;
    padding: 10px 10px;
    background: #f6f7f8;
    justify-content: space-between;
}

.home .trending .container .left_box .header .heading h2 {
    text-transform: uppercase;
    font-size: 16px;
    color: #232323;
    cursor: pointer;
}

.home .trending .container .left_box .header .category {
    display: flex;
    margin-top: 5px;
}

.home .trending .container .left_box .header .category h3 {
    text-transform: capitalize;
    margin-right: 20px;
    color: #232323;
    font-size: 14px;
    cursor: pointer;
    transition: 0.5s;
}

.home .trending .container .left_box .header .category h3:hover {
    color: #cd1e76;
}

.home .trending .container .left_box .products {
    max-width: 100%;
}

.home .trending .container .left_box .products .container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.home .trending .container .left_box .products button{
    margin-top: 20px;
    padding: 10px 30px;
    margin-left: 45%;
    background: none;
    outline: none;
    border: none;
    color: #fff;
    font-weight: 600;
    background: #cd1e76;
    cursor: pointer;
}

.home .trending .container .left_box .products .container   .box {
    width: 195px;
    height: 300px;
    padding: 5px 5px;
    border: 4px solid #f6f7f8;
    margin-top: 20px;
    margin-left: 20px;
    overflow: hidden;
    border-radius: 5px;
}

.home .trending .container .left_box .products .container .box .imgbox {
    display: flex;
}

.home .trending .container .left_box .products .container  .box  .imgbox img{
    width: 190px;
    height: 190px;
    object-fit: cover;
}

.home .trending .container .left_box .products .container .box .imgbox .icon {
    display: flex;
    flex-direction: column;
    margin-left: 0%;
    margin-top: 10px;
    transition: 0.5s;
}

.home .trending .container .left_box .products .container .box:hover .imgbox .icon{
    margin-left: -30%;
}

.home .trending .container .left_box .products .container .box .imgbox .icon .iconbox {
    padding: 10px 10px;
    margin-top: 10px;
    box-shadow: rgba(0,0,0,0.24) 0px 3px 8px;
    z-index: 1;
    background: #fff;
    color: #cd1e76;
    transition: 0.5s;
    cursor: pointer;
}

.home .trending .container .left_box .products .container .box .imgbox .icon .iconbox:hover {
    color: #fff;
    background: #cd1e76;
}

.home .trending .container .left_box .products .container .box .info {
    margin: 5px;
    padding: 10px 10px;
}

.home .trending .container .left_box .products .container .box .info h3 {
    text-transform: uppercase;
    font-size: 14px;
    color: #232323;
    font-weight: 300;
    letter-spacing: 1px;
}

.home .trending .container .left_box .products .container .box .info p {
    color: #cd1e76;
    margin-top: 5px;
    font-size: 14px;
    letter-spacing: 1px;
}

.home .trending .container .left_box .products .container .box .info .btn {
    margin-top: 5px;
    align-items: center;
    padding: 10px 20px;
    margin-left: 20px;
    border: none;
    background: none;
    outline: none;
    background: #fed700;
    color: #232323;
    text-transform: capitalize;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.5s;
}

.home .trending .container .left_box .products .container .box .info .btn:hover {
    background: #cd1e76;
    color: #f6f7f8;
}

.home .trending .container .right_box {
    width: 24%;
    padding: 10px 20px;
    display: flex;
    flex-direction: column;
}

.home .trending .container .right_box .right_container {
    max-width: 100%;
}

.home .trending .container .right_box .right_container .testimonial {
    width: 100%;
    border: 4px solid #f6f7f8;
}

.home .trending .container .right_box .right_container .testimonial .head {
    padding: 10px 10px;
    width: 100%;
    background: #f6f7f8;
}

.home .trending .container .right_box .right_container .testimonial .head h3 {
    text-align: center;
    color: #232323;
    font-size: 16px;
    text-transform: uppercase;
    margin-top: 10px;
    letter-spacing: 1px;
}

.home .trending .container .right_box .right_container .testimonial .detail {
    padding: 10px 20px;
}

.home .trending .container .right_box .right_container .testimonial .detail .img_box img {
    margin-top: 20px;
    width: 100%;
}

.home .trending .container .right_box .right_container .testimonial .detail .info h3{
    text-align: center;
    color: #232323;
    text-transform: uppercase;
    font-size: 14px;
    margin-top: 5px;
}

.home .trending .container .right_box .right_container .testimonial .detail .info h4{
    text-align: center;
    margin-top: 10px;
    color: #cd1e76;
    font-size: 12px;
    font-weight: 300;
    text-transform: uppercase;
}

.home .trending .container .right_box .right_container .testimonial .detail .info p {
    margin-top: 10px;
    text-align: center;
    font-weight: 600;
    color: #232323;
    font-size: 12px;
    margin-bottom: 20px;
}

.home .trending .container .right_box .right_container .newsletter {
    width: 100%;
    background: #f6f7f8;
    margin-top: 20px;
    padding: 10px 10px;
}

.home .trending .container .right_box .right_container .newsletter .head {
    margin-top: 10px;
    width: 100%;
}

.home .trending .container .right_box .right_container .newsletter .head h3 {
    text-align: center;
    color: #232323;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 1px;
}

.home .trending .container .right_box .right_container .newsletter .form {
    margin-top: 10px;
    width: 100%;
}

.home .trending .container .right_box .right_container .newsletter .form p {
    text-align: center;
    text-transform: capitalize;
    color: #8a8a8a;
}

.home .trending .container .right_box .right_container .newsletter .form input {
    margin-top: 10px;
    width: 100%;
    padding: 10px 10px;
    background: none;
    outline: none;
    border: none;
    border: 1px solid #8a8a8a;
    color: #232323;
}

.home .trending .container .right_box .right_container .newsletter .form button {
    margin-top: 10px;
    margin-left: 50px;
    padding: 10px 30px;
    background: none;
    outline: none;
    border: none;
    background: #fed700;
    color: #232323;
    font-weight: 600;
    cursor: pointer;
}

.home .trending .container .right_box .right_container .newsletter .form .icon_box {
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.home .trending .container .right_box .right_container .newsletter .form .icon_box .icon {
    padding: 10px 10px;
    border-radius: 50%;
    border: 1px solid #cd1e76;
    color: #cd1e76;
    margin-bottom: 20px;
    cursor: pointer;
    transition: 0.5s;
}

.home .trending .container .right_box .right_container .newsletter .form .icon_box .icon:hover {
    color: #fff;
    background: #cd1e76;
}

.home .banners {
    padding: 20px 30px;
    width: 100%;
}

.home .banners .container {
    max-width: 100%;
    display: flex;
}

.home .banners .container .left_box {
    width: 24%;
}

.home .banners .container .left_box .box {
    margin-top: 10px;
}

.home .banners .container .left_box .box img {
    width: 100%;
}

.home .banners .container .right_box {
    width: 75%;
    padding: 10px 20px;
}

.home .banners .container .right_box .top {
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.home .banners .container .right_box .bottom {
    max-width: 100%;
    margin-top: 10px;
}

.home .banners .container .right_box .bottom img {
    width: 100%;
    height: 480px;
    object-fit: cover;
}

.home .product_type {
    padding: 20px 30px;
    width: 100%;
}

.home .product_type .container {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.home .product_type .container .box {
    width: 360px;
    height: 490px;
    border: 4px solid #f6f7f8;
    overflow: hidden;
}

.home .product_type .container .box .header {
    width: 100%;
    height: 50px;
    background: #f6f7f8;
    padding: 10px 10px;
    text-align: center;
}

.home .product_type .container .box .header h2 {
    color: #232323;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 600;
}

.home .product_type .container .box .productbox {
    width: 100%;
    height: 100px;
    padding: 10px 10px;
    border: 1px solid #f6f7f8;
    display: flex;
}

.home .product_type .container .box .productbox .img_box img {
    width: 150px;
    height: 100px;
    object-fit: cover;
    padding: 5px 0px;
}

.home .product_type .container .box .productbox .detail {
    margin-left: 30px;
}

.home .product_type .container .box .productbox .detail h3 {
    font-size: 14px;
    color: #8a8a8a;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.home .product_type .container .box .productbox .detail p {
    margin-top: 5px;
    color: #cd1e76;
    letter-spacing: 1px;
    font-weight: 600;
    font-size: 14px;
}

.home .product_type .container .box .productbox .detail .icon {
    margin-top: 10px;
    display: flex;

}

.home .product_type .container .box .productbox .detail .icon button {
    background: none;
    outline: none;
    border: none;
    padding: 8px 10px;
    border: 2px solid #f6f7f8;
    margin-left: 10px;
    border-radius: 20%;
    color: #8a8a8a;
    cursor: pointer;
    transition: 0.5s;
}

.home .product_type .container .box .productbox .detail .icon button:hover {
    background: #f6f7f8;
    color: #232323;
}

/* mobile width */
@media screen and (max-width: 480px) {
  .home {
    padding: 10px;
  }

  /* Banner Section */
  .home .top_banner {
    margin-top: 20px;
    width: 100%;
    height: 200px;
    padding: 20px 10px;
    background-position: top;
  }

  .home .top_banner .content {
    position: static;
    text-align: start;
    padding: 0;
  }

  .home .top_banner .content h2 {
    font-size: 16px;
    margin-bottom: 10px;
  }

  .home .top_banner .content p {
    font-size: 12px;
    margin-bottom: 15px;
  }

  .home .top_banner .content .link {
    padding: 10px 20px;
    font-size: 14px;
  }

  /* Trending Section */
  .home .trending .container {
    flex-direction: column;
    gap: 20px;
  }

  .home .trending .container .left_box,
  .home .trending .container .right_box {
    width: 100%;
    padding: 0;
  }

  .home .trending .container .right_box .top img{
    width: 400px;
    height: 300px;
  }

  .home .trending .container .left_box .header {
    flex-direction: column;
    text-align: center;
    gap: 20px;
  }

  .home .trending .container .left_box .header .category {
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .home .trending .container .left_box .products {
    margin-top: 20px;
  }

  .home .trending .container .left_box .products .container {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .home .trending .container .left_box .products .container .box {
    width: 90%;
    height: auto;
    margin: 0;
  }

  .home .trending .container .left_box .products .container .box .imgbox .icon{
    margin-left: 30%;
    }

    .home .trending .container .left_box .products .container .box:hover .imgbox .icon{
    margin-left: -20%;
    }

  .home .trending .container .left_box .products .container .box .imgbox img {
    width: 100%;
    height: auto;
  }

  .home .trending .container .left_box .products .container .box .info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .home .trending .container .left_box .products button {
    display: block;
    margin-right: 20px;
  }

  /* Banners Section */
  .home .banners .container {
    margin-top: -50px;
    flex-direction: column;
    gap: 5px;
  }

  .home .banners .container .left_box,
  .home .banners .container .right_box {
    width: 100%;
    padding: 5px;
  }

  .home .banners .container .right_box .top {
    flex-direction: column;
    gap: 5px;
  }

  .home .banners .container .right_box .bottom img {
    height: auto;
    width: 100%;
  }

  /* Product Type Section */
  .home .product_type .container {
    display: none;
  }

  /* Newsletter Section */
  .home .trending .container .right_box .newsletter .form {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .home .trending .container .right_box .newsletter .form input{
    width: 100%;
  }

  .home .trending .container .right_box .newsletter .form button {
    width: 75%;
  }

  .home .trending .container .right_box .newsletter .form .icon_box {
    display: flex;
    justify-content: space-around;
    gap: 5px;
  }

  .home .trending .container .right_box .newsletter .form .icon_box .icon {
    padding: 10px;
  }
}
